<script>
import StatusIcon from '@/components/StatusIcon.vue';
import { change_app_config_base, change_app_data, del_app, get_app_info, get_ws_url } from '@/scripts/apps/product';
import _ from 'lodash';
import CryptoJS from 'crypto-js';

export default {
    data() {
        return {
            colors: [
                { color: "#6f7ad3", percentage: 20 },
                { color: "#1989fa", percentage: 40 },
                { color: "#5cb87a", percentage: 60 },
                { color: "#e6a23c", percentage: 80 },
                { color: "#f56c6c", percentage: 100 },
            ],

            hidden: true,
            app_id: '',
            data: {
                app_id: '',
                app_secret: '',

                app_data: {
                    class_member_list: [],
                    
                    _app_config: {
                        committee_loop_time: 0,
                        membership_loop_time: 0,
                    }
                }
            },
            temp: {
                auto_renew_status: true,
                auto_renew_type: 0,

                app_data: {
                    class_member_list: [],
                    temp_data: {
                        committee: '',
                        membership: '',
                    },
                    _app_config: {
                        committee_loop_time: 0,
                        membership_loop_time: 0,
                    }
                },
            },
            temp_: {
                input: {
                    member_name: '',
                }
            },
            firstRefresh: true,

            ws: {
                wsc: null,
                loading: true,
                connect_status: 0,

                loading_list: [],

                client: {
                    connect: {
                        status: false
                    },
                    ip: '',
                },
                client_: {
                    resource_info: {
                        cpu: {
                            usageRate: 0
                        },
                        memory: {
                            usageRate: 0
                        }
                    }
                }
            },

            preview: {
                img: ''
            }
        }
    },
    methods: {
        getMemberIndex(name) {
            if (!name) return;
            for (let i = 0; i < this.temp.app_data.class_member_list.length; i++) {
                if (this.temp.app_data.class_member_list[i].name === name) {
                    return i
                }
            }
        },
        moveMember(name, type) {
            if (!name) return;
            for (let i = 0; i < this.temp.app_data.class_member_list.length; i++) {
                if (this.temp.app_data.class_member_list[i].name === name) {
                    if (type === 1) {
                        // 
                        if (i === 0) {
                            return;
                        }
                        let o = this.temp.app_data.class_member_list[i - 1]
                        this.temp.app_data.class_member_list[i - 1] = this.temp.app_data.class_member_list[i]
                        this.temp.app_data.class_member_list[i] = o;

                        this.temp.app_data.class_member_list = JSON.parse(JSON.stringify(this.temp.app_data.class_member_list));
                        return;
                    } else {
                        // 
                        if (i === this.temp.app_data.class_member_list.length - 1) {
                            return;
                        }
                        let o = this.temp.app_data.class_member_list[i + 1]
                        this.temp.app_data.class_member_list[i + 1] = this.temp.app_data.class_member_list[i]
                        this.temp.app_data.class_member_list[i] = o;

                        this.temp.app_data.class_member_list = JSON.parse(JSON.stringify(this.temp.app_data.class_member_list));
                        return;
                    }
                }
            }
        },
        async changeName(name) {
            if (!name) return;
            for (let i = 0; i < this.temp.app_data.class_member_list.length; i++) {
                if (this.temp.app_data.class_member_list[i].name === name) {
                    const { value: newName } = await this.$swal({
                        input: 'text',
                        inputLabel: '',
                        inputValue: name,
                        showCancelButton: true,
                        confirmButtonText: '',
                        cancelButtonText: '',
                    })
                    this.temp.app_data.class_member_list[i].name = newName;
                    return;
                } 
            }
            this.$swal({
                icon: "warning",
                title: "",
                text: "",
            });
            return;
        },
        async addMember(name) {
            if (!name) return;
            for (let i = 0; i < this.temp.app_data.class_member_list.length; i++) {
                if (this.temp.app_data.class_member_list[i].name === name) {
                    await this.$swal({
                        icon: "warning",
                        title: "",
                        text: "",
                    });
                    return;
                } 
            }
            this.temp.app_data.class_member_list.push({name: name, type: 0});
        },
        async addMembers() {
            const { value: names } = await this.$swal({
                input: 'textarea',
                inputLabel: '',
                inputValue: '',
                showCancelButton: true,
                confirmButtonText: '',
                cancelButtonText: '',
            });
            if (names) {
                names.split('\n').forEach(async name => {
                    await this.addMember(name);
                })
            }
        },
        async delMember(name) {
            if (!name) return;
            for (let i = 0; i < this.temp.app_data.class_member_list.length; i++) {
                if (this.temp.app_data.class_member_list[i].name === name) {
                    this.temp.app_data.class_member_list.splice(i, 1);
                    return;
                } 
            }
            this.$swal({
                icon: "warning",
                title: "",
                text: "",
            })
        },
        async changeRole(name) {
            if (!name) return;
            for (let i = 0; i < this.temp.app_data.class_member_list.length; i++) {
                if (this.temp.app_data.class_member_list[i].name === name) {
                    this.temp.app_data.class_member_list[i].type = this.temp.app_data.class_member_list[i].type === 1 ? 0 : 1;
                }
            }
        },
        async operation(type, eventName, data = null) {
            if (type.substring(0,4) === "send") {
                    if (!this.ws.client.connect.status) {
                    this.$swal({
                        icon: "warning",
                        title: "",
                        text: "",
                        confirmButtonText: "",
                    });
                    return;
                }
            }

            this.$swal({
                icon: "warning",
                title: "",
                text: ` ${eventName} ?`,
                showCancelButton: true,
                confirmButtonText: "",
                cancelButtonText: "",
            }).then(async (res) => {
                if (!res.isConfirmed) {
                    return false;
                }
                const loading = this.$vs.loading({
                    target: this.$refs.card,
                    text: '...'
                })

                let resp = null;
                if (type === 'delete') {
                    resp = await del_app(this.app_id);
                } else if (type === 'change_base') {
                    resp = await change_app_config_base(this.app_id, {
                        app_name: this.temp.app_name,
                        auto_renew_status: this.temp.auto_renew_status ? 1 : 0,
                        auto_renew_type: parseInt(this.temp.auto_renew_type)
                    });
                } else if (type === 'change_lock_pwd') {
                    const { value: pwd } = await this.$swal.fire({
                        input: "password",
                        inputLabel: "",
                        inputPlaceholder: "",
                        showCancelButton: true,
                        confirmButtonText: "",
                        cancelButtonText: "",
                    });
                    if (pwd) {
                        resp = await change_app_data(this.app_id, {
                            _app_config: {
                                lock_pwd: CryptoJS.MD5(pwd + '@xzYYDS').toString(),
                            }
                        })
                    }
                } else if (type === 'change_app_data') {
                    console.log(data)
                    resp = await change_app_data(this.app_id, data)
                } else if (type === 'send_pullData') {
                    this.ws.wsc.send(JSON.stringify({
                        eventType: 'send_pullData',
                    }));
                } else if (type === 'send_msg_small') {
                    
                    // 
                    const { value: message } = await this.$swal.fire({
                        input: "textarea",
                        inputLabel: "",
                        inputPlaceholder: "...",
                        inputAttributes: {
                            "aria-label": "Type your message here"
                        },
                        showCancelButton: true,
                        confirmButtonText: "",
                        cancelButtonText: "",
                    });
                    if (message) {
                        this.ws.wsc.send(JSON.stringify({
                            eventType: 'send_msg_small',
                            data: message
                        }));
                    }
                } else if (type === 'send_msg_big') {
                    // 
                    const { value: message } = await this.$swal.fire({
                        input: "textarea",
                        inputLabel: "",
                        inputPlaceholder: "...",
                        inputAttributes: {
                            "aria-label": "Type your message here"
                        },
                        showCancelButton: true,
                        confirmButtonText: "",
                        cancelButtonText: "",
                    });
                    if (message) {
                        this.ws.wsc.send(JSON.stringify({
                            eventType: 'send_msg_big',
                            data: message
                        }));
                    }
                } else if (type === 'send_screen_shot') {
                    this.ws.wsc.send(JSON.stringify({
                        eventType: 'send_screen_shot',
                    }));
                    this.$toastx({
                        type: "success",
                        title: "",
                    })
                } else if (type === 'send_shutdown_1') {
                    this.ws.wsc.send(JSON.stringify({
                        eventType: 'send_shutdown_1',
                    }));
                } else if (type === 'send_shutdown_2') {
                    this.ws.wsc.send(JSON.stringify({
                        eventType: 'send_shutdown_2',
                    }));
                } else if (type === 'send_lock') {
                    this.ws.wsc.send(JSON.stringify({
                        eventType: 'send_lock',
                    }));
                } else if (type === 'send_unlock') {
                    this.ws.wsc.send(JSON.stringify({
                        eventType: 'send_unlock',
                    }));
                } else if (type === 'reset') {
                    resp = {
                        code: 0,
                        msg: ''
                    }
                } else {
                    this.$swal({
                        icon: "error",
                        title: "",
                        text: "",
                        confirmButtonText: "OK"
                    })
                }

                loading.close();

                if (resp === null) {
                    return;
                }

                if (resp.code !== 1) {
                    this.$swal({
                        icon: "error",
                        title: "",
                        text: resp.msg,
                        confirmButtonText: ""
                    })
                } else if (resp.code === 1) {
                    this.$swal({
                        icon: "success",
                        title: "",
                        text: resp.msg,
                        confirmButtonText: resp.back ? "" : ""
                    }).then(() => {
                        if (resp.back) {
                            this.$router.push({path: resp.back});
                        }
                    })
                }
                
            })
        },
        async refreshData() {
            const firstRefresh = this.firstRefresh;
            this.firstRefresh = false;

            const loading = this.$vs.loading({
                target: this.$refs.card,
            })
            const resp = await get_app_info(this.app_id);
            if (resp.code !== 1) {
                if (resp.code === -1 || resp.code === 403) {
                    this.$swal({
                        icon: "error",
                        title: "",
                        text: "",
                        confirmButtonText: ""
                    }).then(() => {
                        this.$router.back();
                    })
                    return;
                }
                this.$swal({
                    icon: "error",
                    title: "",
                    text: resp.msg,
                    confirmButtonText: ""
                }).then(() => {
                    this.$router.back();
                })
            }

            this.data = resp.data;
            resp.data.auto_renew_status = !!resp.data.auto_renew_status;

            if (firstRefresh || true) this.temp = _.cloneDeep(resp.data);
            loading.close();
        },
        async WsHandler() {
            // get Iot Ws url
            const resp = await get_ws_url()
            const ws_url = resp.data.url;
            // const ws_url = 'ws://127.0.0.1:6001';

            var ws = new WebSocket(ws_url);
            this.ws.wsc = ws;

            ws.onopen = (event) => {
                console.log('[Ws] ', event);

                this.ws.loading = true;
                // 
                ws.send(JSON.stringify({
                    type: 2,
                    app_id: this.data.app_id,
                    app_secret: this.data.app_secret,
                }))
            };

            ws.onmessage = (event) => {
                this.ws.loading = false;

                const data = JSON.parse(event.data);
                if (!data.event || !data.event.name) {
                    return;
                }

                if (data.event.name === 'authorized') {
                    this.ws.connect_status = data.code;

                    if (this.ws.connect_status === 1) {

                        this.ws.loading = true;
                        // 
                        ws.send(JSON.stringify({
                            eventType: 'pullData',
                        }))
                    }
                } else if (data.event.name === 'info') {
                    if (data.code === 1004) {

                        this.ws.loading = true;
                        // 
                        ws.send(JSON.stringify({
                            eventType: 'pullData',
                        }))
                    } else if (data.code === 1005) {
                        // 
                        this.$swal({
                            icon: "success",
                            title: data.msg,
                            confirmButtonText: ""
                        })
                    }
                } else if (data.event.name === 'tell-resource_info') {
                    console.log(data.data)
                    this.ws.client_.resource_info = data.data;
                } else if (data.event.name === 'tell-unlock') {
                    this.$swal({
                        icon: "warning",
                        title: "",
                        text: "",
                        confirmButtonText: "",
                        denyButtonText: "",
                        cancelButtonText: "",
                        showCancelButton: true,
                        showDenyButton: true,
                    }).then((result) => {
                        if (result.isDenied) {
                            this.operation('change_lock_pwd', '');
                        }
                    });
                } else if (data.event.name === 'back-pullData') {
                    this.data.app_data = data.data;
                    if (this.ws.client.connect.status === false && data.client.connect.status === true) {
                        this.$toastx({ type: "success", title: "" })
                    } else if (this.ws.client.connect.status === true && data.client.connect.status === false) {
                        this.$toastx({ type: "warning", title: "" })
                    }
                    this.ws.client = data.client;
                } else if (data.event.name === 'back-screen_shot') {
                    this.$swal.fire({
                        title: "",
                        text: "",
                        imageUrl: "data:image/png;base64," + data.data,
                        imageWidth: 800,
                        imageAlt: "",
                        confirmButtonText: "",
                        cancelButtonText: "",
                        showCancelButton: true,
                    }).then((result) => {
                        if (result.isConfirmed) {
                            this.preview.img = "data:image/png;base64," + data.data;
                        }
                    })
                }

                console.log('[Ws] ', event);
            };

            ws.onerror = (error) => {
                this.$swal({
                    icon: "error",
                    title: "" + error,
                    text: "Iot",
                    showCancelButton: true,
                    confirmButtonText: "",
                    cancelButtonText: "",
                }).then((result) => {
                    if (result.isConfirmed) {
                        this.WsHandler();
                    }
                })
            };

            ws.onclose = (event) => {
                this.$swal({
                    icon: "error",
                    title: "" + event.code,
                    text: "Iot",
                    showCancelButton: true,
                    confirmButtonText: "",
                    cancelButtonText: "",
                }).then((result) => {
                    if (result.isConfirmed) {
                        this.WsHandler();
                    }
                })
            };
        },
        saveImg(base64) {
            let byteCharacters = atob(
                base64.replace(/^data:image\/(png|jpeg|jpg);base64,/, "")
            );
            let byteNumbers = new Array(byteCharacters.length);
            for (let i = 0; i < byteCharacters.length; i++) {
                byteNumbers[i] = byteCharacters.charCodeAt(i);
            }
            let byteArray = new Uint8Array(byteNumbers);
            let blob = new Blob([byteArray], {
                type: undefined,
            });
            let aLink = document.createElement("a");
            aLink.download = ".jpg";
            aLink.href = URL.createObjectURL(blob);
            aLink.click();
        },
        copy(text) {
            navigator.clipboard.writeText(text);
            this.$swal({
                icon: "success",
                title: "",
                text: "",
                confirmButtonText: "OK"
            })
        }
    },
    async mounted() {
        this.app_id = this.$route.params.app_id;
        if (!this.app_id) {
            this.$swal({
                icon: "error",
                title: "",
                text: "",
                confirmButtonText: ""
            }).then(() => {
                this.$router.back();
            })
        }
        await this.refreshData();
        this.WsHandler();
    },
    computed: {
        today_committee() {
            try {
                if (!this.data.app_data.class_member_list) {
                    return '';
                }
                if (this.data.app_data.temp_data.committee) {
                    return this.data.app_data.temp_data.committee;
                }
                let committee_list = [];
                for (let i = 0; i < this.data.app_data.class_member_list.length; i++) {
                    committee_list.push(this.data.app_data.class_member_list[i].name);
                }
                
                if (committee_list.length === 0) {
                    return '';
                }
                return committee_list[this.data.app_data._app_config.committee_loop_time - 1];
            } catch (error) {
                return '';
            }
        },
        today_membership() {
            try {
                if (!this.data.app_data.class_member_list) {
                    return '';
                }
                if (this.data.app_data.temp_data.membership) {
                    return this.data.app_data.temp_data.membership;
                }
                let membership_list = [];
                for (let i = 0; i < this.data.app_data.class_member_list.length; i++) {
                    if (this.data.app_data.class_member_list[i].type === 1) {
                        membership_list.push(this.data.app_data.class_member_list[i].name);
                    }
                }
                if (membership_list.length === 0) {
                    return '';
                }
                return membership_list[this.data.app_data._app_config.membership_loop_time - 1];
            } catch (error) {
                return '';
            }
        },
        committees() {
            if (!this.data.app_data.class_member_list) {
                return [];
            }
            let committee_list = [];
            for (let i = 0; i < this.data.app_data.class_member_list.length; i++) {
                committee_list.push(this.data.app_data.class_member_list[i].name);
            }
            return committee_list;
        },
        memberships() {
            if (!this.data.app_data.class_member_list) {
                return [];
            }
            let membership_list = [];
            for (let i = 0; i < this.data.app_data.class_member_list.length; i++) {
                if (this.data.app_data.class_member_list[i].type === 1) {
                    membership_list.push(this.data.app_data.class_member_list[i].name);
                }
            }
            return membership_list;
        }
    },
    components: {
        StatusIcon
    },
    watch: {
        'ws.loading': {
            immediate: true,
            handler: function(value) {
                this.$nextTick(() => {
                    const target_list = document.querySelectorAll('.ws-loading');
                    if (value) {
                        for (let i = 0; i < target_list.length; i++) {
                            this.ws.loading_list.push (this.$vs.loading({
                                target: target_list[i],
                            }))
                        }
                    } else {
                        for (let i = 0; i < this.ws.loading_list.length; i++) {
                            this.ws.loading_list[i].close();
                        }
                        this.ws.loading_list = [];
                    }
                })
            }
        }
    }
}
</script>

